@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,300;1,400;1,700&display=swap);
*,
::after,
::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
		'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.jumbotron {
	background-image: url(/static/media/hero.e621c18f.jpg);
	background-position: center;
	background-size: cover;
	height: 120vh;
}

.Search-title {
	color: rgb(118, 18, 21);
	font-weight: 700;
	letter-spacing: 0.2rem;
}

.Search-input {
	min-width: 250px;
	padding: 7px 18px;
	display: inline-block;
	margin: 2rem 1rem;
	border-radius: 25px;
	border: none;
	font-size: small;
}

@media only screen and (max-width: 768px) {
	.jumbotron {
		height: 40vh;
	}

	.Search-title {
		color: rgb(8, 187, 2);
		font-weight: 700;
		font-size: 1.8rem;
		letter-spacing: 0.2rem;
	}
}

.Navbar {
	background-color: #8be;
	height: 80px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.Navbar-brand {
	margin-left: 2rem;
}
.Navbar-links {
	display: flex;
	justify-content: flex-end;
	list-style: none;
}

.Navbar-link {
	cursor: pointer;
	font-size: 1.3rem;
	font-weight: 600;
	margin-right: 3.6rem;
}

.Navbar a {
	color: rgb(138, 97, 2);
}

